.option {
  position: relative;
  margin-left: 1rem;
  margin-right: 1rem;
  color: white;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.picker {
  position: absolute;
  top: calc(100% + 1rem);
  left: 0;
}


